












export default {
	data() {
		return {
			detail:'detail-view',
			top:'-999px',
			left:0
		};
	},
	methods:{
		tapStart(res){
			var x = res.touches[0].pageX-50;
			var y = res.touches[0].pageY -50
			this.top=y+'px'
			this.left=x+'px'
		},
		tapEnd(){
			this.detail='detail-view'
			this.top='-999px'
		},
		longpre(){
			this.detail='long'
			this.top=0
			this.left=0
		}
	}
}
